import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Separator } from "@/components/ui/separator";
import { Progress } from "@/components/ui/progress";
import { Phone, CheckCircle, Clock, AlertCircle, TrendingUp } from "lucide-react";
import { Metadata } from "next";

export const metadata: Metadata = {
  title: "Call Renewal",
  description: "Manage and renew customer calls",
};

const CallRenewal = () => {
  // Mock data for renewal calls
  const renewalCalls = [
    {
      id: 1,
      clientName: "John Doe",
      lastCallDate: "2025-09-15",
      renewalDate: "2025-10-15",
      mortgageAmount: "$450,000",
      status: "due",
      daysUntilRenewal: 3,
      priority: "high",
    },
    {
      id: 2,
      clientName: "Jane Smith",
      lastCallDate: "2025-08-20",
      renewalDate: "2025-10-20",
      mortgageAmount: "$320,000",
      status: "pending",
      daysUntilRenewal: 8,
      priority: "medium",
    },
    {
      id: 3,
      clientName: "Michael Johnson",
      lastCallDate: "2025-07-10",
      renewalDate: "2025-10-10",
      mortgageAmount: "$580,000",
      status: "overdue",
      daysUntilRenewal: -2,
      priority: "high",
    },
    {
      id: 4,
      clientName: "Sarah Williams",
      lastCallDate: "2025-09-01",
      renewalDate: "2025-11-01",
      mortgageAmount: "$275,000",
      status: "pending",
      daysUntilRenewal: 20,
      priority: "low",
    },
  ];

  const getStatusColor = (status: string) => {
    switch (status) {
      case "overdue":
        return "bg-red-100 text-red-800";
      case "due":
        return "bg-orange-100 text-orange-800";
      case "pending":
        return "bg-blue-100 text-blue-800";
      default:
        return "bg-gray-100 text-gray-800";
    }
  };

  const getPriorityColor = (priority: string) => {
    switch (priority) {
      case "high":
        return "bg-red-500";
      case "medium":
        return "bg-yellow-500";
      case "low":
        return "bg-green-500";
      default:
        return "bg-gray-500";
    }
  };

  const stats = [
    { label: "Total Renewals", value: "4", color: "text-blue-600" },
    { label: "Overdue", value: "1", color: "text-red-600" },
    { label: "Due Soon", value: "1", color: "text-orange-600" },
    { label: "Completed", value: "2", color: "text-green-600" },
  ];

  return (
    <div className="p-6">
      <div className="max-w-7xl mx-auto">
        {/* HEADER */}
        <div className="flex items-center justify-between mb-8">
          <div>
            <h1 className="text-3xl font-bold">Call Renewal Center</h1>
            <p className="text-gray-600 mt-1">
              Manage and track mortgage renewal calls
            </p>
          </div>
          <Button className="bg-green-600 hover:bg-green-700 flex items-center gap-2">
            <Phone className="w-4 h-4" />
            Schedule Renewal
          </Button>
        </div>

        {/* STATS GRID */}
        <div className="grid grid-cols-4 gap-4 mb-8">
          {stats.map((stat, idx) => (
            <Card key={idx} className="bg-primary-foreground">
              <CardContent className="pt-6">
                <div className="flex items-center justify-between">
                  <div>
                    <p className="text-sm text-gray-600">{stat.label}</p>
                    <p className={`text-3xl font-bold ${stat.color} mt-2`}>
                      {stat.value}
                    </p>
                  </div>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>

        {/* RENEWAL CALLS LIST */}
        <div className="space-y-4">
          <h2 className="text-xl font-bold">Renewal Queue</h2>
          {renewalCalls.map((call) => (
            <Card key={call.id} className="bg-primary-foreground border border-gray-200">
              <CardContent className="p-6">
                <div className="flex items-start justify-between">
                  {/* LEFT SECTION - CLIENT INFO */}
                  <div className="flex-1">
                    <div className="flex items-center gap-3 mb-3">
                      <h3 className="font-semibold text-lg">{call.clientName}</h3>
                      <Badge className={`text-xs font-medium ${getStatusColor(call.status)}`}>
                        {call.status.charAt(0).toUpperCase() + call.status.slice(1)}
                      </Badge>
                      <Badge
                        className={`text-xs font-medium text-white ${getPriorityColor(
                          call.priority
                        )}`}
                      >
                        {call.priority.charAt(0).toUpperCase() + call.priority.slice(1)} Priority
                      </Badge>
                    </div>

                    {/* DETAILS ROW */}
                    <div className="flex items-center gap-4 text-sm text-gray-600 mb-4">
                      <span>Last Call: {call.lastCallDate}</span>
                      <span>•</span>
                      <span>Renewal: {call.renewalDate}</span>
                      <span>•</span>
                      <span className="font-medium text-gray-900">
                        {call.mortgageAmount}
                      </span>
                    </div>

                    {/* PROGRESS BAR */}
                    <div className="flex items-center gap-3">
                      <div className="flex-1">
                        <div className="flex justify-between mb-1">
                          <span className="text-xs text-gray-600">Days until renewal</span>
                          <span
                            className={`text-xs font-medium ${
                              call.daysUntilRenewal <= 0
                                ? "text-red-600"
                                : call.daysUntilRenewal <= 7
                                ? "text-orange-600"
                                : "text-green-600"
                            }`}
                          >
                            {call.daysUntilRenewal > 0 ? `${call.daysUntilRenewal} days` : "Overdue"}
                          </span>
                        </div>
                        <Progress
                          value={Math.max(0, (call.daysUntilRenewal / 30) * 100)}
                          className="h-2"
                        />
                      </div>
                    </div>
                  </div>

                  {/* RIGHT SECTION - ACTIONS */}
                  <div className="flex items-center gap-2 ml-6">
                    <Button variant="outline" size="sm">
                      View Details
                    </Button>
                    <Button className="bg-blue-600 hover:bg-blue-700" size="sm">
                      <Phone className="w-4 h-4 mr-1" />
                      Call Now
                    </Button>
                  </div>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>

        {/* FOOTER INFO */}
        <Card className="mt-8 bg-blue-50 border-blue-200">
          <CardContent className="pt-6">
            <div className="flex items-start gap-3">
              <AlertCircle className="w-5 h-5 text-blue-600 mt-0.5 flex-shrink-0" />
              <div>
                <h4 className="font-semibold text-blue-900 mb-1">Renewal Tips</h4>
                <p className="text-sm text-blue-800">
                  Start renewal conversations 30-45 days before the renewal date. Track all
                  interactions and document client preferences. Follow up within 24 hours of initial
                  contact.
                </p>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  );
};

export default CallRenewal;